.card{
    display: flex;
    justify-content: center;
    flex-direction: column;
    width: 60vmin;
    height: 60vmin;
    transition: all 0.2s ease-in;
    transition: opacity 0.5s ease-in;
    opacity: 0;
}

.card-wrapper{
    margin: 8px;
    border: 1px solid var(--red);
    cursor: pointer;
    transition: all 0.2s ease;
}

.card-wrapper:nth-child(1){
    background: url(../images/urlShortening.png) center center/cover;
}

.card-wrapper:nth-child(2){
    background: url(../images/snake.png) center center/cover;
}

.card-wrapper:nth-child(3){
    background: url(../images/shinzu.png) center center/cover;
}

.card-wrapper:nth-child(4){
    background: url(../images/bini.png) center center/cover;
}

.card-wrapper:nth-child(5){
    background: url(../images/measure.png) center center/cover;
}

.card-wrapper:nth-child(6){
    background: url(../images/shopify.png) center center/cover;
}

.card-pad{
    padding: 24px;
    background-color: var(--yellow);
}

.card:hover{
    opacity: 1;
}

.card-wrapper:hover{
    transform: scale(1.02);    
}

.card-container{
    display: flex;
    justify-content: space-around;
    align-items: center;
    flex-direction: column;
    color: var(--dark);
}

.card-links-container{
    display: flex;
    justify-content: space-evenly;
    margin: 12px 0;
    width: 100%;
}

.card-link{
    background-color: rgba(128, 128, 128, 0.199);
    color: var(--dark);
    font-weight: bolder;
    text-decoration: none;
    padding: 8px;
    border-radius: 15px;
    transition: all 0.2s ease-in;
}

.card-link:active, .card-link:hover{
    background-color: grey;
    color: var(--off-white);    
}

.card-heading{
    font-size: 2rem;
    margin-bottom: 24px;
    text-align: center;
}

.card-description{
    margin: 16px;
    line-height: 24px;
}

.card-techstack{
    display: inline-flex;
    justify-content: space-evenly;
    align-items: center;
    width: 100%;
    height: 48px;
    padding: 48px 0;
}

.card-img{
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover;
}

@media screen and (max-width: 960px) {
    .card{
        width: 90vw;
        height: 90vw;
        font-size: 0.75rem;
    }

    .card-heading{
        font-size: 1.25rem;
    }
    
    .card-description{
        margin: 5px;

    }

    .card-techstack{
        width: 100%;
        height: 48px;
        padding: 24px 0;
    }
    
}

@media screen and (max-width: 320px) {
    .card{
        font-size: 0.75rem;
    }

    .card-heading{
        font-size: 1.25rem;
        margin: 0;
    }
    
    .card-description{
        margin: 5px;
        line-height: normal;

    }

    .card-techstack{
        width: 100%;
        height: 48px;
        padding: 6px 0;
    }
    
    .card-links-container{
        margin: 0;
    }
}